.CV {

    .CVcontent{
        background: linear-gradient(115deg, transparent 50%, $color-1 100%);
        min-height: $minHeight;
        width: 100%;
        min-height: 500px;
        border-radius: $borderRadius;
        position: relative;
        overflow: hidden;
        
        &:after{
            content: "";
            position: absolute;
            height: 100%;
            width: inherit;

            background: url("../../media/imgTest.jpg") no-repeat left/cover;
            z-index: -1;
            filter: brightness(50%);
        }

        .content {
            position: absolute;
            top: 20%;
            left: 13%;
            font-family: $font-1;

            h1 {
                font-size: 5rem;
                font-family: $font-3;
                color: $color-5;
                text-transform: uppercase;
                text-shadow: 3px 6px 2px rgb(15, 15, 15);
            }
            h2 {
                font-size: 2.5rem;
                padding: 1.5rem 0 7rem;
                color: $color-5;
                text-transform: uppercase;
                text-shadow: 1px 4px 1px rgb(15, 15, 15);
            }
            .pdf {
                a {
                    font-size: 1.5rem;
                    background: $color-3;
                    padding: 1rem 2rem;
                    border-radius: $borderRadius;
                    box-shadow: 0 5px 1px rgb(15, 15, 15);
                    transition: 0.2s ease;

                    &:hover {
                        background: $color-4;
                        box-shadow: 0 5px 1px rgb(15, 15, 15);
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 850px) {
    .CVcontent {
        margin: 0 !important;
    }
}
@media screen and (max-width: 600px) {
    .CVcontent {
        &::after {
            background: url("../../media/imgTest.jpg") no-repeat 60% 50%/cover !important;
            h1 {
                font-size: 3rem !important;
            }
            h2 {
                font-size: 1.8rem !important;
            }
        }
    }
}